var helpers = {
    addZeros: function (n) {
        return (n < 10) ? '0' + n : '' + n;
    }
};

function sliderInit() {
    var $slider = $('.feedbacks__slider');
    $slider.each(function() {
        var $sliderParent = $(this).parent();
        $(this).slick({
            slidesToShow: 3,
            slidesToScroll: 1,
            dots: false,
            infinite: true,
            prevArrow: $('.feedbacks__prev'),
            nextArrow: $('.feedbacks__next'),
            responsive: [
                {
                    breakpoint: 1601,
                    settings: {
                        slidesToShow: 2,
                    }
                },
                {
                    breakpoint: 1024,
                    settings: {
                        slidesToShow: 3,
                    }
                },
                {
                    breakpoint: 500,
                    settings: {
                        slidesToShow: 1,
                    }
                },
            ]
        });

        if ($(this).find('.feedback-card').length > 1) {
            $(this).siblings('.slides-numbers').show();
        }

        $(this).on('afterChange', function(event, slick, currentSlide){
            $sliderParent.find('.slides-numbers .active').html(helpers.addZeros(currentSlide + 1));
        });

        var sliderItemsNum = $(this).find('.slick-slide').not('.slick-cloned').length;
        $sliderParent.find('.slides-numbers .total').html(helpers.addZeros(sliderItemsNum));

    });

};

sliderInit();


$(document).ready(function() {

    // Burger
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header__right').toggleClass('header__right--active')
        $('body').toggleClass('fixed-body')
    })

    // Select
    $('.select__list-default').click(function () {
        $(this).parent('.select__wrap').addClass('select__wrap--active')
    })

    $('.select__list li').click(function () {
        var currentele = $(this).html()
        $(this).parents('.select__wrap').find('.select__current').html(currentele)
        $(this).siblings(".select__list li").removeClass("li--active")
        $(this).addClass("li--active")
    })

    let selectWrap = $('.select__wrap')

    $(document).mouseover(function (e) {
        if ( !selectWrap.is(e.target) && selectWrap.has(e.target).length === 0

        ) {
            selectWrap.removeClass('select__wrap--active')
        }
    })


    // Date


})